import TitlePage from '../TitlePage';
import styles from './MasterLayout.module.scss';
import { useSelector } from 'react-redux';
import { themeClass } from '../../Theme';

const cx = require('classnames/bind').bind(styles);
function MasterLayout({ children, mode, id='', titlePage='', subtitlePage=''}) {
    const theme = useSelector((state) => state.theme);
    const themeMode = themeClass[theme];

    var bgColor;
    var titlePageColor;
    var subtitlePageColor;
    if (mode === 1) {
        bgColor = themeMode.bgColor;        
        titlePageColor = themeMode.textDetail;
        subtitlePageColor = themeMode.textColor;
    } else if (mode === 2) {
        bgColor = themeMode.secondaryPage.bgColor;        
        titlePageColor = themeMode.secondaryPage.textDetail;
        subtitlePageColor = themeMode.secondaryPage.textColor;
    }
    const masterLayoutClass = ['master-layout', 'overflow-auto', bgColor, 'py-5', 'mh-100'];
    return (
        <section className={cx(masterLayoutClass)} id={id}>
            <div className={cx('container')}>
                {titlePage ? (<TitlePage
                    title={titlePage}
                    subtitle={subtitlePage}
                    titleColor={titlePageColor}
                    subtitleColor={subtitlePageColor}
                />) : ''}
                {children}
            </div>
        </section>
    );
}

export default MasterLayout;
