import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
    CDBSidebar,
    CDBSidebarContent,
    CDBSidebarFooter,
    CDBSidebarHeader,
    CDBSidebarMenu,
    CDBSidebarMenuItem,
} from 'cdbreact';
import { publicRoutes } from '~/routes';
import styles from './Sidebar.module.scss';
import { themeClass } from '../../Theme';
import useScrollspy from './useScrollspy';
import { themeToggle } from '../../redux/actions';
import { Sun, Moon } from '~/assets/icons';

const cx = require('classnames/bind').bind(styles);
const Sidebar = () => {
    const ids = publicRoutes.map((route) => route.path);
    const activeId = useScrollspy(ids, 250);

    const theme = useSelector((state) => state.theme);
    const dispatch = useDispatch();
    const themeMode = themeClass[theme].sideBar;

    const [isChecked, setIsChecked] = useState(true);
    const handleToggle = () => {
        dispatch(themeToggle({ theme: isChecked ? 'light' : 'dark' }));
        setIsChecked(!isChecked);
    };
    return (
        <div className={cx('sidebar', 'shadow')}>
            <CDBSidebar breakpoint={'1200'} className={cx(themeMode.textColor, 'fs-6', themeMode.bgColor)}>
                <CDBSidebarHeader
                    prefix={<i className="fa fa-bars fa-large "></i>}
                    className="sidebar-header d-none d-sm-block"
                >
                    {' '}
                    <a href="/" className={cx('text-decoration-none', themeMode.textColor)}>
                        Minh Nguyen
                    </a>
                </CDBSidebarHeader>
                <CDBSidebarContent className="sidebar-content">
                    <CDBSidebarMenu>
                        {publicRoutes.map((route, index) => (
                            <a href={`#${route.path}`} key={index} className={cx(themeMode.textColor)}>
                                <CDBSidebarMenuItem
                                    className={cx('item-menu', route.path === activeId ? 'item-menu-active' : '')}
                                    icon={route.icon}
                                >
                                    {route.component}
                                </CDBSidebarMenuItem>
                            </a>
                        ))}
                        <CDBSidebarHeader
                            className={cx('d-flex', 'align-items-center', 'justify-content-center')}
                            prefix={
                                <div className={cx('mode-toggle-button')}>
                                    <input
                                        type="checkbox"
                                        id="darkmode-toggle"
                                        checked={isChecked}
                                        onChange={handleToggle}
                                    />
                                    <label for="darkmode-toggle">
                                        <span className={cx('sun')}>
                                            <Sun />
                                        </span>
                                        <span className={cx('moon')}>
                                            <Moon />
                                        </span>
                                    </label>
                                </div>
                            }
                        ></CDBSidebarHeader>
                    </CDBSidebarMenu>
                </CDBSidebarContent>
                <CDBSidebarFooter style={{ textAlign: 'center' }}></CDBSidebarFooter>
            </CDBSidebar>
        </div>
    );
};

export default Sidebar;
