import { useSelector } from 'react-redux';
import { themeClass } from '../../Theme';
import MasterLayout from '~/components/MasterLayout';
import style from './Contact.module.scss';
import Tippy from '@tippyjs/react/headless';
import Form from 'react-bootstrap/Form';
import Col from 'react-bootstrap/Col';
import Button from '~/components/Button';

const cx = require('classnames/bind').bind(style);

function Contact() {
    const theme = useSelector((state) => state.theme);
    const themeMode = themeClass[theme];

    const contactInfo = [
        'contact-info',
        themeMode.textColor,
        'container',
        'fs-6',
        'text-md-start',
        'text-center',
        'col-xl-3',
        'col-md-5',
        'col-xs-12',
        'mb-3',
    ];
    const iconFollow = ['d-flex', 'justify-content-md-start', 'justify-content-center', 'gap-3', 'fs-5'];
    const sendNote = [
        'send-note',
        themeMode.textColor,
        'container',
        'col-xl-9',
        'col-md-7',
        'col-xs-12',
        'order-2',
        'text-md-start',
        'text-center',
    ];
    const inputNote = [themeMode.bgColor, 'input-note', 'border-0', 'order-1', 'rounded-0'];
    return (
        <MasterLayout id="contact" mode={2} titlePage="Contact" subtitlePage="Get in Touch">
            <div className={cx('row', 'd-flex', 'flex-wrap')}>
                <div className={cx(contactInfo)}>
                    <div className={cx('mb-3')}>
                        <h3 className="fs-4">Address</h3>
                        <p className={cx(themeMode.textDetail)}>53 p 1, Xun Qu, Cm M, ng Nai</p>
                    </div>
                    <div className={cx('contact-phone-email', themeMode.textDetail)}>
                        <p>
                            <span>
                                <i class="fas fa-phone-alt"></i>
                            </span>
                            0356195495
                        </p>
                        <p>
                            <span>
                                <i class="fas fa-envelope"></i>
                            </span>
                            <p>khanhminhjk283@gmail.com</p>
                        </p>
                    </div>
                    <div>
                        <h3 className="fs-4">Follow me</h3>
                        <div className={cx(iconFollow)}>
                            <Tippy placement="top" content="Facebook">
                                <a href="facebook.com" className={cx(themeMode.textDetail)}>
                                    <i className="fab fa-facebook-f"></i>
                                </a>
                            </Tippy>
                            <Tippy placement="top" content="Github">
                                <a href="facebook.com" className={cx(themeMode.textDetail)}>
                                    <i className="fab fa-github"></i>
                                </a>
                            </Tippy>
                            <Tippy placement="top" content="Linkedin in">
                                <a href="facebook.com" className={cx(themeMode.textDetail)}>
                                    <i className="fab fa-linkedin-in"></i>
                                </a>
                            </Tippy>
                        </div>
                    </div>
                </div>
                <div className={cx(sendNote)}>
                    <h3 className="fs-4">Send me a note</h3>
                    <Form>
                        <Form.Group className="mb-4 row g-4 col-12" controlId="exampleForm.ControlInput1">
                            <Col xl="6" xs="12">
                                <Form.Control size="lg" type="text" placeholder="Name" className={inputNote} />
                            </Col>
                            <Col xl="6" xs="12">
                                <Form.Control size="lg" type="email" placeholder="Email" className={inputNote} />
                            </Col>
                            <Col xs="12">
                                <Form.Control as="textarea" className={inputNote} rows={5} />
                            </Col>
                        </Form.Group>
                        <p className="text-center">
                            <Button>Send Message</Button>
                        </p>
                    </Form>
                </div>
            </div>
        </MasterLayout>
    );
}

export default Contact;
