import Button from '~/components/Button';
import styles from './AboutMe.module.scss';
import MasterLayout from '~/components/MasterLayout';
import { useSelector } from 'react-redux';
import { themeClass } from '../../Theme';
import { EN } from '../../lan';

const cx = require('classnames/bind').bind(styles);
function AboutMe() {
    const theme = useSelector((state) => state.theme);
    const themeMode = themeClass[theme];

    const aboutMeContainerClass = [
        'about-me-container',
        'container',
        'h-100',
        'd-flex',
        'flex-column',
        'justify-content-between',
    ];
    const aboutMeTitleClass = ['about-me-title', themeMode.textColor, 'text-center', 'text-lg-start', 'fs-3'];
    const aboutMeDetailClass = ['about-me-detail', themeMode.textDetail, 'text-center', 'text-lg-start', 'fs-6'];
    const aboutMeSummaryClass = [
        'about-me-summary',
        'col-sm-12',
        'col-xl-4',
        'col-lg-5',
        themeMode.textColor,
        'fs-6',
        'mb-3',
        'mt-5',
    ];
    const aboutMeExpClass = ['about-me-exp', themeMode.textDetail, 'row', 'col-sm-12', 'mb-5'];
    const aboutMeExpItemClass = [
        'about-me-exp-item',
        themeMode.textDetail,
        'col-sm-6',
        'col-lg-3',
        'mb-5',
        'position-relative',
    ];
    return (
        <MasterLayout id="about-me" mode={1} titlePage={EN.aboutMe.title} subtitlePage={EN.aboutMe.subtitle}>
            <div className={cx(aboutMeContainerClass)}>
                <div className={cx('row', 'mb-5')}>
                    <div className={cx(aboutMeSummaryClass)}>
                        <ul>
                            <li className={themeMode.borderColor}>
                                <span>Name:</span>
                                {EN.aboutMe.name}
                            </li>
                            <li className={themeMode.borderColor}>
                                <span>Email:</span>
                                <a href={'mailto:' + EN.aboutMe.email}>{EN.aboutMe.email}</a>
                            </li>
                            <li className={themeMode.borderColor}>
                                <span>Age:</span>
                                {EN.aboutMe.age}
                            </li>
                            <li className={themeMode.borderColor}>
                                <span>From:</span>
                                {EN.aboutMe.location}
                            </li>
                        </ul>
                        <Button>Download CV</Button>
                    </div>
                    <div className={cx('col-sm-12', 'col-xl-8', 'col-lg-7', themeMode.textColor, 'mt-5')}>
                        <h1 className={cx(aboutMeTitleClass)}>
                            {EN.aboutMe.detail1} <span>{EN.aboutMe.name}</span>
                            {EN.aboutMe.detail2}
                        </h1>
                        <p className={cx(aboutMeDetailClass, 'mb-2')}>{EN.aboutMe.detail3}</p>
                        <p className={cx(aboutMeDetailClass)}>{EN.aboutMe.detail4}</p>
                    </div>
                </div>
                <div className={cx(aboutMeExpClass)}>
                    <div className={cx(aboutMeExpItemClass)}>
                        <h4>6</h4>
                        <span className={cx(themeMode.textColor)}>Months Experiance</span>
                    </div>
                    <div className={cx(aboutMeExpItemClass)}>
                        <h4>3</h4>
                        <span className={cx(themeMode.textColor)}>Project Done</span>
                    </div>
                    <div className={cx(aboutMeExpItemClass)}>
                        <h4>1</h4>
                        <span className={cx(themeMode.textColor)}>Months Experiance</span>
                    </div>
                    <div className={cx(aboutMeExpItemClass)}>
                        <h4>1</h4>
                        <span className={cx(themeMode.textColor)}>Months Experiance</span>
                    </div>
                </div>
            </div>
        </MasterLayout>
    );
}

export default AboutMe;
