import { useSelector } from 'react-redux';
import { themeClass } from '../../../Theme';
import style from '../Resume.module.scss';

const cx = require('classnames/bind').bind(style);
function Card({children, title, place, time}) {
    const theme = useSelector((state) => state.theme);
    const themeMode = themeClass[theme];
    return (
        <div className={cx(themeMode.bgItem,'card-resume', 'card', 'rounded-0','border-0', 'mb-4')}>
            <div className="card-body">
                <span className={cx('badge-years', 'badge', 'rounded-0')}>{time}</span>
                <h4 className={cx(themeMode.textColor)}>{title}</h4>
                <p className={cx('subtitle-by')}>{place}</p>
                <p className={cx(themeMode.textDetail, 'fs-6')}>
                {children}
                </p>
            </div>
        </div>
    );
}

export default Card;
