import { useSelector } from 'react-redux';
import { themeClass } from '../../Theme';
import styles from './Home.module.scss';
import classNames from 'classnames/bind';
import Card from 'react-bootstrap/Card';
import homeBackground from '~/assets/images/home-bg.jpg';
import Button from '~/components/Button';

const cx = classNames.bind(styles);
function Home() {
    const theme = useSelector((state) => state.theme);
    const themeMode = themeClass[theme];
    const homeClass = ['vh-100', 'overflow-auto', 'position-relative'];
    const cardClass = [themeMode.textColor, themeMode.home.bgColor, 'card-home', 'col-12', 'd-flex', 'flex-row', 'rounded-0'];
    const cardBodyClass = [
        'card-body-content',
        'col-lg-7',
        'col-10',
        'd-flex',
        'flex-column',
        'justify-content-center',
        'align-items-end',
    ];
    return (
        <section className={cx(homeClass)} id="home">
            <div className={cx('home-background-image')}></div>
            <div className={cx('home-content', 'container', 'py-5')}>
                <Card className={cx(cardClass)} style={{ height: '800px' }}>
                    <Card.Body className={cx(cardBodyClass)}>
                        <h1>NGUYEN HOANG KHANH MINH</h1>
                        <span className={cx(themeMode.secondaryPage.textColor, 'content-underline')}></span>
                        <h2 className="">
                            <span className={cx(themeMode.textDetail, 'fs-2')}>I'm a </span>Web Developer
                        </h2>
                        <Button to="#contact">Contact me</Button>
                    </Card.Body>
                    <Card.Img
                        src={homeBackground}
                        className={cx('card-img-avatar', 'rounded-0', 'col-4', 'd-none', 'd-lg-block')}
                    />
                </Card>
            </div>
        </section>
    );
}

export default Home;
