import styles from './TitlePage.module.scss';
import classNames from 'classnames/bind';

function TitlePage({ title, subtitle, titleColor, subtitleColor }) {
    const cx = classNames.bind(styles);
    const backgroundClass = [
        'd-flex',
        'position-relative',
        'justify-content-center',
    ];
    const titleClass = [
        'background-title', 
        'text-center', 
        'w-100',
        ...(titleColor ? [titleColor] : [])
    ];
    const subtitleClass = [
        'subtitle',
        'text-uppercase',
        'text-center',
        'position-absolute',
        'top-50',
        'start-50',
        'translate-middle',
        'w-100',
        ...(subtitleColor ? [subtitleColor] : []),
    ];
    return (
        <div className={cx(backgroundClass)}>
            <h1 className={cx(titleClass)}>{title}</h1>
            <p className={cx(subtitleClass)}>{subtitle}</p>
        </div>
    );
}

export default TitlePage;
