import { useSelector } from 'react-redux';
import { themeClass } from '../../Theme';
import MasterLayout from '~/components/MasterLayout';
import style from './WhatIDo.module.scss';

const cx = require('classnames/bind').bind(style);
function WhatIDo() {
    const theme = useSelector((state) => state.theme);
    const themeMode = themeClass[theme];

    const whatIDoClass = ['d-flex', 'flex-wrap', 'justify-content-center', 'align-items-center'];
    const whatIDoItemClass = ['what-i-do-.', 'd-flex', 'mb-5', 'col-lg-6', 'px-5'];
    const whatIDoIconClass = ['what-i-do-icon', 'me-3', themeMode.bgColor, 'rounded-0', 'shadow'];
    const whatIDoTitleClass = ['what-i-do-title', themeMode.textColor, 'text-start', 'fs-5'];
    const whatIDoDetailClass = ['what-i-do-detail', themeMode.textDetail, 'text-start', 'fs-6'];
    return (
        <MasterLayout id="what-i-do" mode={2} titlePage="Services" subtitlePage="What I Do">
            <div className={cx(whatIDoClass)}>
                <div className={cx(whatIDoItemClass)}>
                    <div className={cx(whatIDoIconClass)}>
                        <i class="fas fa-image"></i>
                    </div>
                    <div className={cx('icon')}>
                        <h4 className={cx(whatIDoTitleClass)}>Landing page</h4>
                        <p className={cx(whatIDoDetailClass)}>
                            Design responsive website at the request of cilents, using HTML, CSS, JS, ReactJS.
                        </p>
                    </div>
                </div>
                <div className={cx(whatIDoItemClass)}>
                    <div className={cx(whatIDoIconClass)}>
                        <i class="fas fa-code"></i>
                    </div>
                    <div className={cx('icon')}>
                        <h4 className={cx(whatIDoTitleClass)}>Front-end skill</h4>
                        <p className={cx(whatIDoDetailClass)}>
                            Good at HTML, CSS, JS, ReactJS, Redux, Bootstrap to build responsive website.
                        </p>
                    </div>
                </div>
                <div className={cx(whatIDoItemClass)}>
                    <div className={cx(whatIDoIconClass)}>
                        <i class="fas fa-database"></i>
                    </div>
                    <div className={cx('icon')}>
                        <h4 className={cx(whatIDoTitleClass)}>Back-end skill</h4>
                        <p className={cx(whatIDoDetailClass)}>
                            Good at PHP, MySQL, NodeJS and Laravel framework to build server and database.
                        </p>
                    </div>
                </div>
                <div className={cx(whatIDoItemClass)}>
                    <div className={cx(whatIDoIconClass)}>
                        <i class="fas fa-desktop"></i>
                    </div>
                    <div className={cx('icon')}>
                        <h4 className={cx(whatIDoTitleClass)}>Web development</h4>
                        <p className={cx(whatIDoDetailClass)}>
                            Combine front-end and back-end skill to build a website with good UI/UX and high
                            performance.
                        </p>
                    </div>
                </div>
            </div>
        </MasterLayout>
    );
}

export default WhatIDo;
